import React, { Component } from 'react';
import './App.css';
import fire from './fire.js';
import { Redirect } from 'react-router-dom';
import img from './img/logo.gif'
class App extends Component {

  state = {
    text: "",
    text1: ""
  }

  handleText = e => {
    this.setState({
      text: e.target.value
    })
  }
  handleText1 = e => {
    this.setState({
      text1: e.target.value
    })
  }
  handleSubmit = e => {
    let messageRef = fire.database().ref('messages').orderByKey().limitToLast(100);
    fire.database().ref('messages').push(this.state);
    // <Redirect to="/staysave" />
    this.setState({
      text: "",
      text1: ""
    }
    )
    
  }

  render() {
    return (
      <div className="container">
        <div class="card" >
          <div class="card-body">
            <img src={img} style={{ height: "50px" }} />
            <b style={{ color: "green" }}>Find Near By [Covid19] Patients </b>
            <b style={{ color: "green" }}># Stay At Home üôè</b>
          </div>
        </div>
        <div class="card">
          <div class="card-body">
            <h5 className="card-title">Login</h5>
            <h6 className="card-subtitle mb-2 text-muted">
              <b style={{ color: "red" }}>Note:</b> Not required <b style={{ color: "green" }}>Registation</b> directly login in your
                Gmail/Facebook/GitHub credential</h6>
            <div className="form-group" />
            <label for="exampleInputEmail1">Email address</label>
            <input type="email" className="form-control" onChange={this.handleText} id="exampleInputEmail1" aria-describedby="emailHelp" required />

            <div className="form-group" />
            <label for="exampleInputPassword1">Password</label>
            <input type="password" className="form-control" onChange={this.handleText1} id="exampleInputPassword1" required />

            <br />
            <button type="submit" className="btn btn-primary btn-block" onClick={this.handleSubmit}>Submit</button>
          </div>
        </div>


      </div>

    );
  }
}


export default App;

